import logo from '@/static/mbcImg/my/logo.png'
import ce from '@/static/mbcImg/my/2.png'
import shouc from '@/static/mbcImg/news/shouc.png'
import zan2 from '@/static/mbcImg/news/zan2.png';
import kong from '@/static/mbcImg/my/kong.png';
import comp from '@/static/mbcImg/my/comp.png';
export default {
	data() {
		return {
			logo:logo,
			ce:ce,
			shouc:shouc,
			zan2:zan2,
			list:[],
			nums:'',
			kong:kong,
			comp:comp,
		}
	},
	mounted() {

	},
	onLoad() {
		
	},
	filters: {
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y + '.' + MM + '.' + d ;
		}
	},
	created() {
		//被收藏列表
		this.Collection();
		this.shareEachPage(); // 分享
	},
	methods: {
		Collection(){//被收藏列表
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/myLikeOrFollowList?userId='+landRegistLG.user.id+'&page=1&type=1', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.nums=response.data.content.count
						this.list=response.data.content.list
						// this.$store.commit('setMation', this.List); // 更新vuex
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	},
	computed: {
	},
}
